.Button {
  display: block;
  padding: 10px 20px;
  border-radius: 4px;
  border: 1px solid #ccc;
  color: #000;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px; }

.Button:focus {
  outline: none; }

.Button:active {
  box-shadow: inset 2px 2px 1px rgba(0,0,0, .3); }

.Button:disabled {
  background: #ccc;
  color: #000;
  cursor: not-allowed; }

.Button--Error {
  background-color: rgba(240, 87, 108, 1); }

.Button--Success {
  background-color: rgba(161, 240, 69, 1); }

.Button--Primary {
  background-color: #2884f6;
  color: #fff; }
